<template>
  <div class="list-container">
    <transition-group
      v-infinite-scroll="load"
      :infinite-scroll-immediate="false"
      style="overflow:auto"
      class="list"
      tag="ul"
      name="fade"
    >
      <li v-for="item in list" :key="item._id">
        <MyPageBlogItem :blog="item" :liked="liked" :del="del" :edit="edit" />
      </li>
    </transition-group>
    <EmtyTips v-show="!list.length" />
  </div>
</template>

<script>
export default {
  inheritAttrs: false,
  props: {
    list: {
      type: Array,
      default() {
        return [];
      }
    },
    del: Boolean,
    edit: Boolean,
    liked: Boolean,
    load: Function
  }
};
</script>

<style lang="scss" scoped>
.list-container {
  .list {
    width: 100%;
    height: 100%;
    max-height: calc(100vh - 98px - 12px);
    list-style: none;
  }
}
</style>