<template>
    <nav class="nav">
        <ul @click="handleItem" class="nav-container">
            <li
                class="nav-item"
                v-for="(item, index) in leftNavList"
                :key="index"
                :data-key="item.name"
                :class="{ 'nav-active': item.isActive }"
            >
                {{ item.title }}
            </li>
            <li
                class="nav-item refresh-container"
                @click.stop=""
                title="刷新列表"
            >
                <el-button
                    v-if="isRefresh"
                    class="btn"
                    type="primary"
                    size="medium"
                    plain
                    :loading="isRefresh"
                ></el-button>
                <div v-else class="refresh" @click.stop="handleRefresh">
                    <i class="el-icon-refresh"></i>
                </div>
            </li>
        </ul>
    </nav>
</template>

<script>
export default {
    props: {
        leftNavList: {
            type: Array,
            default() {
                return [];
            }
        },
        handleItem: Function,
        handleRefresh: Function,
        isRefresh: Boolean
    }
};
</script>

<style lang="scss" scoped>
.nav {
    user-select: none;
    margin-right: 12px;
    width: 100%;
    height: 100%;
    font-size: 14px;
    z-index: 2; 
    // 上一次修改999
    position: relative;
    .nav-container {
        background-color: #fff;
        display: flex;
        width: 100%;
        height: 100%;
        flex-direction: column;
        li {
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            vertical-align: middle;
        }
        .nav-item {
            font-weight: bold;
            width: 100%;
            color: $uni-text-color;
            height: 30px;
            box-sizing: border-box;
            line-height: 30px;
            border-bottom: 1px solid #fff;
            padding: 0 8px;
            text-align: center;
            cursor: pointer;
            @extend .hover-nav;

            &:first-of-type {
                border-top: 1px solid #fff;
            }
            .btn {
                width: 100%;
                height: 100%;
                border: 0;
                outline: none;
                border-radius: 0;
                background-color: #fff;
            }
            .refresh {
                &:hover {
                    background-color: #fff;
                }
                .el-icon-refresh {
                    font-weight: bold;
                    color: $uni-color-primary;
                }
            }
        }
        .refresh-container {
            padding: 0;
            margin: 0;
        }
        .nav-active {
            background-color: $uni-color-error;
            color: #fff;
        }
    }
    ol,
    ul {
        list-style: none;
    }
}
</style>