export default {
  methods: {
    handleView(id) {
      this.$router.push("/show-blog/" + id);
    },
    handleDelete(id) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          lockScroll: false,
          type: "warning"
        })
        .then(async() => {
          let msg = await this.$store.dispatch("deleteBlog", id);
          let { ok } = msg;
          if (ok === 1) {
            this.$message({
              type: "success",
              showClose: true,
              message: "删除成功!"
            });
            this.$store.commit("DELETEMYBLOG", id);
          } else {
            throw new Error("删除失败");
          }
        })
        .catch(e => {
          this.$message({
            type: "info",
            showClose: true,
            message: e.toString() === "cancel" ?
              "已取消删除" : e.toString()
          });
          return;
        });
    },
    handleEdit(id) {
      this.$router.push({
        path: "/add-blog?type=edit&id=" + id
      });
    },
    async handleLiked(id) {
      await this.$store.dispatch("toggleLike", {
        blogID: id,
        userID: this.user._id,
        key: 'likes'
      });
    }
  }
}