import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home'
import HomeIndex from '@/views/HomeIndex'



Vue.use(VueRouter)


const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    name: 'user',
    path: '/user',
    meta: {
      title: '用户信息'
    },
    component: () => import('@/views/User'),
  },
  {
    name: 'add-blog',
    path: '/add-blog',
    meta: {
      title: '添加/修改'
    },
    component: () => import('@/views/BlogAdd'),
  },
  {
    name: 'show-blog',
    path: '/show-blog/:id',
    component: () => import('@/views/BlogShow'),
  },
  {
    name: 'register',
    path: '/register',
    meta: {
      title: "注册"
    },
    component: () => import('@/views/Register'),

  },
  {
    name: 'login',
    path: '/login',
    meta: {
      title: "登录"
    },
    component: () => import('@/views/Login'),
  },
  {
    path: '/my-page',
    component: () => import('@/views/MyPage'),
    children: [
      {
        name: 'my-page',
        path: '',
        component: () => import('@/views/MyPageIndex'),
        meta: {
          title: '我的'
        },
      },
      {
        name: 'my-page/search',
        path: '/my-page/search',
        component: () => import('@/views/MyPageUserSeearch'),
      }
    ]
  },
  {
    path: "*",
    meta: {
      keepAlive: true,
      title: '404找不到页面...'
    },
    component: () => import('@/views/NotFound')
  }
]

const headerNav = [
  {
    path: '/download',
    component: () => import('@/views/Download'),
    value: '关于',
    meta: {
      keepAlive: true,
      title: '关于'
    },
    name: 'download'
  },
  {
    path: '/home',
    component: Home,
    value: '首页',
    meta: {
      keepAlive: true,
    },
    children: [
      {
        path: '',
        name: 'home',
        component: HomeIndex,
        meta: {
          keepAlive: true,
          title: '首页'
        }
      },
      {
        name: 'search',
        path: '/home/search',
        component: () => import('@/views/HomeSearch'),
      }
    ]
  }
]


const admin = [
  {
    path: '/admin',
    component: () => import('@/views/Admin'),
    value: '管理员',
    meta: {
      title: '管理中心'
    },
    name: 'admin'
  }
]





const router = new VueRouter({
  routes: [...routes, ...headerNav]
})



router.beforeEach((to, from, next) => {
  // 修改titl
  if (to.meta.title) {
    document.title = to.meta.title
  }

  // 守卫
  let isLogin = localStorage.getItem("BLOG_TOKEN") ? true : false;
  if (to.path === "/login" || to.path === '/register' || to.name === "show-blog") {
    next()
  } else if ((to.path === '/my-page' || to.path === '/edit') && !isLogin) {
    next("/login")
  } else {
    next()
  }
})

export default router;

export { headerNav, admin }