<template>
    <div class="todos">
        <el-input
            placeholder="add todos"
            v-model.trim="input"
            @keydown.enter.native="handleAdd"
            clearable
        >
        </el-input>
        <div class="enty" v-if="!$store.state.todos.todos.length">
            暂无数据!
        </div>
        <transition-group v-else tag="ul" name="el-fade-in">
            <li
                :key="item._id"
                :title="item.createdAt | date1"
                class="todo-item"
                @click.stop="toggle(item._id)"
                v-for="item in $store.state.todos.todos"
            >
                <p class="text" :class="{ done: item.done }">
                    {{ item.task }}
                </p>

                <p @click.stop="remove(item._id)" class="el-icon-error"></p>
            </li>
        </transition-group>
    </div>
</template>

<script>
export default {
    data() {
        return {
            input: ""
        };
    },
    methods: {
        async handleAdd() {
            if (this.input === "") return;
            await this.$store.dispatch("addTodos", {
                task: this.input
            });
            this.$nextTick(function() {
                this.input = "";
            });
        },
        async toggle(id) {
            await this.$store.dispatch("toggle", id);
        },
        async remove(id) {
            await this.$store.dispatch("remove", id);
        }
    },
    created() {
        this.$store.dispatch("getTodos");
    }
};
</script>

<style lang="scss" scoped>
.todos {
    width: 100%;
    max-height: 350px;
    overflow: auto;
    &::-webkit-scrollbar {
        display: none;
    }
    ul {
        box-sizing: border-box;
        padding: 1rem 0;
        .todo-item {
            background-color: #fff;
            margin-top: 3px;
            display: flex;
            cursor: pointer;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            font-size: 14px;
            position: relative;
            // line-height: calc(14px * 1);
            word-break: break-all;
            padding: 5px 1rem;
            transition: all 0.25s;
            &:hover {
                background-color: #667aca;
                .text {
                    color: #fff;
                }
                .el-icon-error {
                    visibility: visible;
                }
            }
            .el-icon-error {
                visibility: hidden;
                color: $uni-color-warning;
                &:hover {
                    color: red;
                }
            }
        }
        .done {
            text-decoration: line-through;
            background-color: bisque;
        }
    }
    .enty {
        padding: 1rem;
        text-align: center;
        font-size: 14px;
        font-weight: bold;
    }
}
</style>