
const tab = {
    state: {
        indexTab: [
            {
                name: "hot",
                oldScroll: 0,
                isActive: true,
                title: '最热'
            },
            {
                name: "new",
                oldScroll: 0,
                isActive: false,
                title: '最新'
            }
        ]

    },

    mutations: {


    },
    actions: {
        async setIndexTab({ state }, name) {
            state.indexTab.forEach(v => v.name === name ? v.isActive = true : v.isActive = false)
        },
        async  setIndexTabScroll({ state }, { name, scrollHeight }) {
            state.indexTab.forEach(v => v.name === name ? v.oldScroll = scrollHeight : "")
        }
    }
}



export default tab