import axios from 'axios'
import qs from 'qs'
import config from '@/config'
import { Message, Loading } from 'element-ui'
const ajax = axios.create({
  baseURL: config.baseURL
});

//节流 防止重复提示
let lastStatusCode = null

function reSetLastCode(code) {
  lastStatusCode = code
  let timer = setTimeout(() => {
    lastStatusCode = null
    clearTimeout(timer)
  }, 3000);
}
// 拦截Ajax
let times = 0
  //请求拦截
ajax.interceptors.request.use(request => {
    let token = localStorage.getItem('BLOG_TOKEN');

    request.headers.Authorization = token

    start(); //start loading

    times++
    return request
  }, error => {
    return Promise.reject(error)
  })
  //返回拦截
ajax.interceptors.response.use(response => {
  times--
  if (times === 0) end()

  return response
}, error => {
  times--
  if (times === 0) end();

  let { status, data } = error.response
  if ((status == 500 || status == 401) && lastStatusCode !== status) {
    reSetLastCode(status)
    Message({
      message: data,
      type: 'error',
      showClose: true
    })

    if (status == 401) localStorage.removeItem('BLOG_TOKEN')
  }
  return Promise.reject(error)
})


export default async function({ type = "GET", url = "", data = {}, headers = {} }) {
  type = type.toUpperCase();

  if (type === 'GET') {
    // let dataStr = ''; //数据拼接字符串
    // Object.keys(data).forEach(key => {
    //   if (data[key] !== undefined)
    //     dataStr += key + '=' + data[key] + '&';
    // })

    // if (dataStr !== '') {
    //   dataStr = dataStr.substr(0, dataStr.lastIndexOf('&')); //切掉最后一个&
    //   url = url + '?' + dataStr;
    // }
    let query = qs.stringify(data)
    if (query.trim() !== "")
      url = url + '?' + query
  }



  // 判断请求
  if (type === "GET") {
    return await ajax.get(url, { headers: {...headers } }).then(res => {
      return res.data
    })
  }

  if (type === "POST") {
    return await ajax.post(url, data, { headers: {...headers } }).then(res => {
      return res.data
    })
  }
}


function start(msg) {
  Loading.service({
    text: msg || '',
    lock: false,
  });
}


function end() {
  Loading.service({}).close();
}