<template>
  <div class="right">
    <el-carousel height="150px" indicator-position="none">
      <el-carousel-item v-for="(item, index) in $store.state.blog.swiperList" :key="index">
        <span>{{ item.tips }}</span>
        <img v-img:normal :src="item.img_url" class="img" />
      </el-carousel-item>
    </el-carousel>
    <div class="tags" @click.stop="handleTags">
      <el-tag
        v-for="(item, index) in $store.state.blog.allTypeLists"
        :key="index"
        :data-type="item.name"
        type="success"
        size="small"
      >{{ item.name }}</el-tag>
    </div>
    <Todos />
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: true
    };
  },
  mounted() {
    this.$store.dispatch("getBlogTypes");
  },
  methods: {
    handleTags(e) {
      let type = e.target.dataset.type;

      if (this.$store.state.blog.indexTypeNavLists.length) {
        let item = this.$store.state.blog.indexTypeNavLists.filter(
          v => v.name === type
        )[0];

        if (item) {
          this.$store.dispatch("setLeftNavList", item.name);
        } else {
          this.$message({
            message: "没想好要做什么",
            showClose: true,
            type: "error"
          });
        }
      } else {
        this.$router.push("/login");
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.right {
  @media screen and (max-width: 1020px) {
    display: none;
  }
  .img {
    height: 100%;
    width: auto;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n + 1) {
    background-color: #d3dce6;
  }

  width: 300px;
  height: 100%;
  background-color: #fff;
  box-sizing: border-box;
  position: relative;
  padding: 1rem;
  .tags {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: space-around;
    padding: 1rem 0;
    span {
      margin-bottom: 5px;
      cursor: pointer;
    }
  }
}
</style>