<template>
    <div class="left-nav-container">
        <div class="bg" @click.stop="onclick"></div>
        <div class="sm-nav-outer">
            <HomeIndexLeftNav
                :leftNavList="navList"
                :handleRefresh="handleRefresh"
                :handleItem="handleItem"
            />
        </div>
    </div>
</template>

<script>
export default {
    props: {
        onclick: Function,
        navList: Array
    },
    methods: {
        handleItem(e) {
            let key = e.target.dataset.key;
            key && this.$store.dispatch("setLeftNavList", key);
        },
        async handleRefresh() {
            this.$store.dispatch("getLeftNavList", true);
        }
    }
};
</script>

<style lang="scss" scoped>
.left-nav-container {
    position: absolute;
    left: 0;
    top: 0;
    background-color: #fff;
    box-sizing: border-box;
    width: 40%;
    height: 100vh;
    .bg {
        width: 100%;
        height: 100%;
        height: 100vh;
        width: 100vw;
        left: 0;
        z-index: 998;
        background-image: linear-gradient(
            $uni-color-error,
            #fff,
            $uni-color-error
        );
        filter: blur(20px);
        position: fixed;
        opacity: 0.5;
    }

    .sm-nav-outer {
        z-index: 2;
        z-index: 999;
        position: absolute;
        width: 40vw;
        height: 100%;
        list-style: none;
        box-sizing: border-box;
        padding: 1rem 0;
        user-select: none;
        padding-top: 44px;
        overflow-x: hidden;
        overflow-y: auto;
        line-height: 22px;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
        background-color: #fff;
    }
}
</style>