<template>
  <div @click.stop="handleView(blog._id)" class="my-page-blog-item">
    <div class="title column-item">
      <p>{{ blog.title }}</p>
    </div>
    <div class="detail column-item">
      <span class="markdown" v-if="blog.markdown">{{ blog.markdown | MarkdownCode }}</span>
    </div>
    <div class="handle-time column-item">
      <div class="time-views">
        <span class="time">{{ blog.createdAt | date1 }}</span>
        <span class="views">
          阅读数
          <span class="views-target">{{ blog.views }}</span>
        </span>
        <span class="comments">
          评论数
          <span class="comments-target">{{ blog.comments ? blog.comments.length : 0 }}</span>
        </span>
      </div>
      <div class="handle">
        <span v-show="edit" @click.stop="handleEdit(blog._id)" class="edit">编辑</span>
        <span v-show="del" @click.stop="handleDelete(blog._id)" class="delete">删除</span>
        <span v-show="liked" @click.stop="handleLiked(blog._id)" class="liked">取消收藏</span>
      </div>
    </div>
  </div>
</template>

<script>
import { blogMixin } from "@/utils/mixins";
export default {
  mixins: [blogMixin],
  props: {
    blog: {
      type: Object,
      require: true
    },
    // handleView: Function,
    // handleLiked: Function,
    // handleDelete: Function,
    // handleEdit: Function,
    del: Boolean,
    edit: Boolean,
    liked: Boolean
  }
};
</script>

<style lang="scss" scoped>
.my-page-blog-item {
  width: 100%;
  user-select: none;
  letter-spacing: 1px;
  padding: 12px 24px;
  box-sizing: border-box;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  -webkit-box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.04);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.04);
  cursor: pointer;
  overflow: hidden;
  @extend .hover;

  &:first-of-type {
    border-top: 1px solid $uni-border-color;
  }

  &:hover {
    div.handle-time {
      div.handle {
        display: block;
      }
    }
  }
  .column-item {
    width: 100%;
  }
  div.title {
    margin-bottom: 6px;
    letter-spacing: 0;
    p {
      font-family: "Microsoft YaHei";
      font-weight: bolder;
      width: 100%;
      color: $uni-text-color;
      font-size: 18px;
      max-height: calc(18px * 1.3333333);
      line-height: calc(18px * 1.3333333);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

  div.detail {
    .markdown {
      width: 100%;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      word-break: break-all;
      white-space: normal;
      -webkit-line-clamp: 2;
      overflow: hidden;
      line-height: calc(14px * 1.3333333);
      letter-spacing: 1px;
      font-size: 14px;
      max-height: 37.3333324px;
    }
  }

  div.handle-time {
    padding-top: 6px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    div.time-views {
      span {
        vertical-align: bottom;
      }
      span.time {
        padding-right: 0.5rem;
        color: $uni-text-color-grey;
        font-size: 12px;
        border-right: 1px solid $uni-bg-color-grey-more;
      }

      span.views {
        border-right: 1px solid $uni-bg-color-grey-more;
        padding: 0 0.5rem;
        span.views-target {
          color: $uni-color-primary;
          font-size: 12px;
        }
      }

      span.comments {
        padding: 0 0.5rem;
        span.comments-target {
          color: $uni-color-primary;
          font-size: 12px;
        }
      }
    }

    div.handle {
      white-space: nowrap;
      display: none;
      font-size: 16px;
      letter-spacing: 1px;
      margin-right: 1rem;
      span.edit {
        padding: 0 3px;
        &:hover {
          color: $uni-color-error;
        }
      }

      span.delete {
        border-left: 1px solid $uni-bg-color-grey-more;
        padding: 0 3px;
        &:hover {
          color: $uni-color-error;
        }
      }
      .liked {
        letter-spacing: 0;
      }
      &:hover {
        color: $uni-color-warning;
      }
    }
    @media screen and (max-width: 500px) {
      .handle {
        display: none;
      }
    }
  }
}
</style>