import Vue from 'vue'
import Vuex from 'vuex'
import header from '@/store/module/header'
import user from '@/store/module/user'
import blog from '@/store/module/blog'
import tab from '@/store/module/tab'
import todos from '@/store/module/todos'

Vue.use(Vuex)


let store = new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',
  state: {
  },
  mutations: {
  },
  actions: {
  },
  modules: {
    header, user, blog, tab, todos
  }
})


// 节流
let dispatch = store.dispatch
const reduceRqForm = {
  getLeftNavList: 1,
  getBlogTypes: 1
}
function newDis(type, obj) {
  if (reduceRqForm[type] === 0) return
  if (reduceRqForm[type]) {
    reduceRqForm[type] = 0
    let timer = setTimeout(() => {
      reduceRqForm[type] = 1
      clearTimeout(timer)
    }, 5000)
  }
  return dispatch.call(store, type, obj)
}

store.dispatch = newDis

export default store
