<template>
  <div class="main">
    <transition-group tag="ul" class="index-list" name="fade">
      <li
        v-for="item in middleBlogList"
        class="blog-item"
        :key="item._id"
        @click="handleView(item._id)"
      >
        <h3 class="blog-title column-item">{{ item.title }}</h3>
        <div class="blog-detail column-item">
          <span class="markdown">{{ item.markdown | MarkdownCode }}</span>
          <div class="blog-thumbnails" v-show="item.thumbnails.length">
            <img :src="item.thumbnails.length ? item.thumbnails[0].src : ''" />
          </div>
        </div>
        <div class="blog-user-views">
          <div class="anthor">
            <p href="#" @click.stop="handleUSER(item.userid)">
              <img
                v-img:avatar
                :src="baseIMGURL + item.userid + '?byuserid=1'"
              />
              <span v-root :data-userid="item.userid">
                {{ item.username }}
              </span>
            </p>
          </div>
          <div class="views-like">
            <span>
              收藏
              <span>{{ item.likes.length }}</span>
            </span>
            <span>
              阅读
              <span>{{ item.views }}</span>
            </span>
            <span>
              评论
              <span>{{ item.comments.length }}</span>
            </span>
          </div>
        </div>
      </li>
    </transition-group>
    <li v-if="!middleBlogList.length">
      <EmtyTips />
    </li>
  </div>
</template>

<script>
export default {
  props: {
    middleBlogList: Array
  },
  mounted() {},
  methods: {
    handleView(id) {
      this.$router.push("/show-blog/" + id);
    }
  }
};
</script>

<style lang="scss" scoped>
.main {
  background-color: #fff;
  max-width: 760px;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  margin-right: 1rem;
  box-sizing: border-box;
  @media screen and (max-width: 500px) {
    margin: 0 0.5rem;
    margin-bottom: 0.5rem;
    height: calc(100vh - 56px - 0.5rem);
  }
  .index-list {
    height: calc(100vh - 56px - 12px);
    overflow: auto;
  }
  .blog-item {
    color: $uni-text-color;
    width: 100%;
    box-sizing: border-box;
    font-size: 16px;
    letter-spacing: 1px;
    background-color: #fff;
    padding: 1rem 24px 13px 24px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    border-bottom: 1px solid $uni-border-color;
    position: relative;
    -webkit-box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.04);
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.04);
    cursor: pointer;
    overflow: hidden;
    @extend .hover;

    .blog-title {
      font-size: 18px;
      font-weight: bold;
      color: $uni-text-color;
      max-height: calc(18px * 1.3333333);
      line-height: calc(18px * 1.3333333);
      margin-top: 0;
      width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      letter-spacing: 0;
      white-space: nowrap;
    }
    .blog-detail {
      display: flex;
      flex-direction: row;
      max-height: 63px;
      .blog-thumbnails {
        margin-right: 1rem;
        vertical-align: middle;
        img {
          border-radius: $uni-border-radius-base;
          height: 63px;
          width: 63px;
        }
      }
      span.markdown {
        margin-right: 1rem;
        width: 100%;
        font-size: 14px;
        line-height: calc(14px * 1.5);
        letter-spacing: 1px;
        max-height: 63px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
      }
    }
    .column-item {
      margin-bottom: 5px;
    }
    div.blog-user-views {
      font-size: 16px;
      color: $uni-text-color;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
      div.anthor {
        height: 24px;
        p {
          height: 24px;
          display: flex;
          flex-direction: row;
          align-items: center;

          img {
            width: 24px;
            height: 24px;
            border-radius: $uni-border-radius-circle;
            margin-right: 6px;
          }

          span {
            font-size: 14px;
            color: #3d3d3d;
          }
        }
      }

      div.views-like {
        margin-left: 25px;
        & > span {
          font-size: 12px;
          color: $uni-text-color;
          padding: 0 5px;
          &:nth-of-type(2) {
            border-left: 1px solid $uni-bg-color-grey-more;
            border-right: 1px solid $uni-bg-color-grey-more;
          }
          span {
            color: $uni-color-primary;
          }
          &::before {
            color: $uni-text-color;
            font-size: 16px;
            font-weight: bold;
          }
        }
      }
    }
  }
  list-style: none;
}
</style>