import { headerNav, admin } from '@/router'

const header = {
    state: {
        headerNavRouter: headerNav,
        searchValue: "",
        hideAllNav: false,
        addAdminRoute: false //是否已经添加路由
    },
    mutations: {
        ['SET_SEARCH_VALUE'](state, value) {
            state.searchValue = value
        },
        //防止移动端hover事件触摸后不隐藏
        ['HIDENAV'](state, boolean) {
            let timeOut = setTimeout(() => {
                state.hideAllNav = boolean
                clearTimeout(timeOut)
            }, 250);
        },
        ['ADDADMINROUTE'](state) {
            state.addAdminRoute = true
            state.headerNavRouter = [...state.headerNavRouter, ...admin]
        }
    },
    actions: {
    }
}



export default header