<template>
    <div class="home-index">
        <div class="left-container">
            <div class="left-outer">
                <HomeIndexLeftNav
                    :leftNavList="$store.state.blog.indexTypeNavLists"
                    :handleItem="handleItem"
                    :handleRefresh="handleRefresh"
                    :isRefresh="isRefresh"
                />
            </div>
        </div>
        <div class="main-right">
            <HomeIndexMiddle :middleBlogList="$store.state.blog.indexBlogs" />
            <HomeIndexRight />
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            leftNavList: [],
            isRefresh: false
        };
    },
    inheritAttrs: false,
    created() {
        this.getLeftNavList();
    },
    methods: {
        //重新获取nav列表
        async handleRefresh() {
            if (this.isRefresh) return;
            this.isRefresh = true;
            await this.getLeftNavList(true);
            this.$message({
                showClose: true,
                message: "刷新成功",
                type: "success"
            });
            let timer = setTimeout(() => {
                this.isRefresh = false;
                clearTimeout(timer);
            }, 2500);
        },
        handleItem(e) {
            let key = e.target.dataset.key;
            this.$store.dispatch("setLeftNavList", key);
        },
        async getLeftNavList(force) {
            let data =
                (await this.$store.dispatch("getLeftNavList", force)) || [];
        }
    }
};
</script>

<style lang="scss" scoped>
.home-index {
    display: flex;
    max-width: 1180px;
    width: 100%;
    margin: 0 auto;
    flex-direction: row;
    position: relative;
    box-sizing: border-box;
    padding: 12px 0;
    .left-container {
        width: 100%;
        max-width: 96px;
        margin-right: 1rem;
        height: 100%;
        .left-outer {
            width: 100%;
            max-width: 96px;
            position: fixed;
        }
        @media screen and (max-width: 550px) {
            display: none;
        }
    }
    .main-right {
        display: flex;
        width: 100%;
        height: 100%;
        flex-direction: row;
        .rigth-fixd {
            width: 100%;
            height: 100%;
            max-width: 500px;
        }
    }
}
</style>