import http from '@/ajax'

const user = {
  state: {
    name: 'tgc',
    user: {},
    viewUser: {},
    isLogin: false,
    //我的信息页选项卡
    MyPageLeftNavList: []

  },

  mutations: {
    ["SETUSER"](state, user) {
      state.user = user
      state.isLogin = true

    },
    ["RESETUSER"](state) {
      localStorage.removeItem("BLOG_TOKEN");
      localStorage.removeItem("BLOG_INDEX_NAV_TYPE_LIST");
      localStorage.removeItem("BLOG_MYNAVLIST");
      state.user = {}
      state.isLogin = false
    },
    //我的信息页选项卡
    ['RESETMYNAV'](state) {
      state.MyPageLeftNavList = [{
          title: "个人中心",
          name: "个人中心",
          component: "MyPageInfo",
          isActive: true
        },
        {
          title: "我的收藏",
          name: "我的收藏",
          component: "MyPageLiked",
          isActive: false
        },
        {
          name: "我的博客",
          title: "我的博客",
          component: "MyPageBlog",
          isActive: false
        },
        {
          name: "我的关注",
          title: "我的关注",
          component: "MyPageFocusPeople",
          isActive: false
        }
      ]
    }
  },
  actions: {
    async logout({ commit }) {
      return await http({
        url: '/user-service/logout'
      })
    },

    async changePassword({ commit }, { prevpassword, newpasswprd }) {
      return await http({
        url: '/user-service/change-password',
        data: {
          prevpassword,
          newpasswprd
        },
        type: "POST"
      })
    },

    async changeUser({ commit }, { name }) {
      return await http({
        url: '/user-service/change-user',
        data: {
          name
        },
        type: "POST"
      })
    },

    async setMyPageLeft({ state, commit }, key) {
      if (state.MyPageLeftNavList.length === 0) {
        await commit('RESETMYNAV')
      }
      state.MyPageLeftNavList.forEach(v => {
        if (v.name == key) {
          if (v.isActive === true) return;
          v.isActive = true;
        } else {
          v.isActive = false;
        }
      });
    },
    async changeAvatar({ state }, formData) {

      let data = await http({
        url: '/file-service/upload?type=avatar',
        data: formData,
        type: "POST",
        headers: { 'Content-Type': 'multipart/form-data' }
      })
      state.user.avatar = data.src;
    },

    async Login({ state, commit }, body) {

      let data = await http({ url: '/user-service/login', data: body, type: "post" })

      let { token, user } = data

      if (token) {
        commit('SETUSER', user)
        localStorage.setItem('BLOG_TOKEN', token)
      }
      return data
    },

    async Register({ state, commit }, body) {

      let data = await http({ url: '/user-service/create-user', data: body, type: "post" })

      let { token, user } = data

      if (token) {
        commit('SETUSER', user)
        localStorage.setItem('BLOG_TOKEN', token)
      }

      return data
    },

    async getUser({ commit }, id) {

      let url = "/user-service/user"
      if (id) {
        return await http({ url, data: { id: id } });
      } else {
        let user = await http({ url });
        commit('SETUSER', user)
      }
    },
  }
}



export default user