import config from '@/config'

class RegisterComponents {
  constructor() {

  }

  install(Vue, options) {
    Vue.directive('root', {
      inserted: function(el) {
        if (el.dataset.userid === '5ea570439839bf5fa40c7ec9') {
          el.id = 'super-user'
        }
      },
      componentUpdated: function(el) {
        if (el.dataset.userid === '5ea570439839bf5fa40c7ec9') {
          el.id = 'super-user'
        }
      }
    })


    Vue.directive('img', {
      inserted: function(el, binding) {
        switch (binding.arg) {
          case 'avatar':
            el.onerror = function() {
              this.src = require('@/assets/default.png')
              this.onerror = null
            }
            break;
          case 'normal':
            el.onerror = function() {
              this.src = require('@/assets/error.png')
              this.onerror = null
            }
            break;
          default:
            break;
        }
      }
    })
  }
}


export default new RegisterComponents()