import http from '@/ajax'


const todos = {
    state: {
        todos: []
    },
    mutations: {
        ["NEWSET"](state, data) {
            state.todos = data
        },
        ["ADDTODO"](state, todo) {
            state.todos = [...state.todos, todo]
        },
        ["SORT"](state) {
            let doneList = state.todos.filter(v => v.done)
            let DedoneList = state.todos.filter(v => !v.done)
            DedoneList.sort((a, b) => b.createdAt - a.createdAt)
            doneList.sort((a, b) => b.createdAt - a.createdAt)
            state.todos = [...DedoneList, ...doneList]
        },
        ["REMOVE"](state, id) {
            state.todos = state.todos.filter(v => v._id !== id)
        },
        ["TOGGLE"](state, id) {
            state.todos.forEach(v => v._id === id ? v.done = !v.done : "")
        }
    },
    actions: {
        async addTodos({ commit }, body) {
            
            let data = await http({ url: "/todo-service/add-todo", data: body, type: "POST" })
            await commit('ADDTODO', data)
            commit('SORT')
            return data
        },
        async getTodos({ commit }, body) {
            let data = await http({ url: "/todo-service/list" })
            await commit('NEWSET', data)
            commit('SORT')
            return data
        },
        async toggle({ commit }, id) {
            let data = await http({ url: "/todo-service/toggle", data: { id } })
            await commit('TOGGLE', id)
            commit('SORT')
            return data
        },
        async remove({ commit }, id) {
            let data = await http({ url: "/todo-service/remove", data: { id } })
            commit('REMOVE', id)
            return data
        },
    }
}



export default todos

