<template>
  <div class="my-page-blog">
    <div class="order">
      <dl class="order-list flex">
        <dt>排序:</dt>
        <dd
          @click.stop="
            myBlogForm.forEach(v =>
              v.title === item.title ? (v.active = true) : (v.active = false)
            )
          "
          v-for="(item, index) in myBlogForm"
          :key="index"
          :style="{ color: item.active ? 'red' : '', cursor: 'pointer' }"
        >
          {{ item.title }}
          <span v-show="item.title !== '默认' && item.active" class="order-by">
            <i
              @click.stop="myBlogForm.forEach(v => (v.active ? (v.o = 1) : ''))"
              :style="{ color: item.o === 1 ? 'red' : '' }"
              class="by-item el-icon-caret-top"
            ></i>
            <i
              @click.stop="
                myBlogForm.forEach(v => (v.active ? (v.o = -1) : ''))
              "
              :style="{ color: item.o === -1 ? 'red' : '' }"
              class="by-item el-icon-caret-bottom"
            ></i>
          </span>
        </dd>
      </dl>
    </div>
    <MyPageBlogContainer
      :del="true"
      :edit="true"
      :load="load"
      :list="$store.state.blog.myBlogs"
    />
  </div>
</template>

<script>
/**
 * myBlogForm: state => state.blog.myBlogForm,
 */
import { Message, Loading } from "element-ui";
export default {
  data() {
    return {
      loading: false,
      type: "normal"
    };
  },
  watch: {
    myBlogForm: {
      handler(newForm, o) {
        this.prev();
      },
      deep: true
    }
  },
  methods: {
    prev() {
      this.loading = false;
      this.$store.commit("RESETMYBLOGS");
      this.load();
    },
    async load() {
      if (this.loading) return;
      this.loading = true;

      let { key, o } = this.myBlogForm.filter(v => v.active)[0];

      let l = this.$store.state.blog.myBlogs.length;

      let query = {
        rs: 0,
        l,
        n: 0,
        ob: { key, o }
      };

      let data = await this.$store.dispatch("getMyBlog", query);
      // 返回长度=0
      if (data.length === 0) {
        Message({
          type: "success",
          message: "已加载全部"
        });
        let timeout = setTimeout(() => {
          this.loading = false;
          clearTimeout(timeout);
        }, 3000);
        return;
      }
      this.loading = false;
    }
  },
  destroyed() {
    this.loading = false;
  }
};
</script>

<style lang="scss" scoped>
.my-page-blog {
  width: 100%;
  height: 100%;
  overflow: hidden;
  .order {
    width: 100%;
    height: 42px;
    box-sizing: border-box;
    line-height: 42px;
    white-space: nowrap;
    .order-list {
      width: 50%;
      font-size: 14px;
      flex-direction: row;
      align-items: center;
      * {
        margin-right: 0.5rem;
      }
      dt {
        color: $uni-text-color-grey;
      }
      // dd {
      // }
    }
  }
}
.order-by {
  display: inline-flex;
  flex-direction: column;
  vertical-align: middle;
  .by-item {
    cursor: pointer;
    color: #333;
    &:hover {
      color: red;
    }
  }
}
</style>