import { mapState } from "vuex";
import config from '@/config'



class GlobalMixIn {
  constructor() {

  }

  install(Vue, options) {
    Vue.mixin({
      data() {
        return {
          baseIMGURL: config.baseIMGURL
        }
      },
      computed: {
        ...mapState({
          headerNav: state => state.header.headerNavRouter,
          searchValue: state => state.header.searchValue,
          isLogin: state => state.user.isLogin,
          user: state => state.user.user,
          hideAllNav: state => state.header.hideAllNav,
          myBlogForm: state => state.blog.myBlogForm,
        })
      },
      methods: {
        //预留点击用户获取信息
        handleUSER(id) {
          this.$router.push('/user?id=' + id)
        }
      },
    })

  }
}


export default new GlobalMixIn()